import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';

function ApplicantEdit() {
  const { applicantId } = useParams();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  
  // 
  const [formData, setFormData] = useState({
    name: '',
    address: '',
    desired_occupation: '',
    desired_location: '',
    birthdate: '',
    email: '',
    phone_number: '',
    gender: '',
    nationality: '',
    employment_status: '',
    available_date: '',
    employment_period: '',
    medical_history: '',
    disability_certificate: '',
    tattoo: '',
    tattoo_details: '',
    criminal_record: '',
    clothing_size: '',
    commute_or_dormitory: '',
    commute_method: '',
    commute_area: '',
    factory_experience: '',
    experience_details: '',
    desired_working_hours: '',
    recent_applications: '',
    most_important_point: '',
    important_point_details: '',
    desired_salary: '',
    height: '',
    weight: ''
  });

  // 
  useEffect(() => {
    const fetchApplicant = async () => {
      try {
        setLoading(true);
        const response = await fetch(`/api/applicants/${applicantId}`);
        
        if (!response.ok) {
          throw new Error(`API error: ${response.status}`);
        }
        
        const data = await response.json();
        
        // 
        setFormData({
          name: data.name || '',
          address: data.address || '',
          desired_occupation: data.desired_occupation || '',
          desired_location: data.desired_location || '',
          birthdate: data.birthdate || '',
          email: data.email || '',
          phone_number: data.phone_number || '',
          gender: data.gender || '',
          nationality: data.nationality || '',
          employment_status: data.employment_status || '',
          available_date: data.available_date || '',
          employment_period: data.employment_period || '',
          medical_history: data.medical_history || '',
          disability_certificate: data.disability_certificate || '',
          tattoo: data.tattoo || '',
          tattoo_details: data.tattoo_details || '',
          criminal_record: data.criminal_record || '',
          clothing_size: data.clothing_size || '',
          commute_or_dormitory: data.commute_or_dormitory || '',
          commute_method: data.commute_method || '',
          commute_area: data.commute_area || '',
          factory_experience: data.factory_experience || '',
          experience_details: data.experience_details || '',
          desired_working_hours: data.desired_working_hours || '',
          recent_applications: data.recent_applications || '',
          most_important_point: data.most_important_point || '',
          important_point_details: data.important_point_details || '',
          desired_salary: data.desired_salary || '',
          height: data.height || '',
          weight: data.weight || ''
        });
        
        setLoading(false);
      } catch (error) {
        console.error('Error fetching applicant details:', error);
        setError(error.message);
        setLoading(false);
      }
    };

    fetchApplicant();
  }, [applicantId]);

  // 
  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [id]: value
    }));
  };

  // 
  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      setLoading(true);
      setError(null);
      
      const response = await fetch(`/api/applicants/${applicantId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || `API error: ${response.status}`);
      }

      const data = await response.json();
      console.log('Success:', data);
      
      // 
      navigate(`/applicants/${applicantId}`);
    } catch (error) {
      console.error('Error:', error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };

  // 
  const formStyle = {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '20px',
  };

  const formGroupStyle = {
    marginBottom: '15px',
  };

  const labelStyle = {
    display: 'block',
    marginBottom: '5px',
    fontWeight: 'bold',
  };

  const inputStyle = {
    width: '100%',
    padding: '8px',
    border: '1px solid #ddd',
    borderRadius: '4px',
  };

  const buttonStyle = {
    padding: '10px 15px',
    backgroundColor: '#4CAF50',
    color: 'white',
    border: 'none',
    borderRadius: '4px',
    cursor: 'pointer',
    fontSize: '16px',
  };

  const errorStyle = {
    color: 'red',
    marginBottom: '15px',
  };

  if (loading && !Object.values(formData).some(value => value)) {
    return <div>...</div>;
  }

  return (
    <div style={formStyle}>
      <h2></h2>
      {error && <div style={errorStyle}>{error}</div>}
      
      <form onSubmit={handleSubmit}>
        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px' }}>
          {/*  */}
          <div>
            <h3></h3>
            
            <div style={formGroupStyle}>
              <label htmlFor="name" style={labelStyle}>: <span style={{ color: 'red' }}>*</span></label>
              <input
                type="text"
                id="name"
                value={formData.name}
                onChange={handleChange}
                style={inputStyle}
                required
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="address" style={labelStyle}>:</label>
              <input
                type="text"
                id="address"
                value={formData.address}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="birthdate" style={labelStyle}>:</label>
              <input
                type="date"
                id="birthdate"
                value={formData.birthdate}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="email" style={labelStyle}>:</label>
              <input
                type="email"
                id="email"
                value={formData.email}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="phone_number" style={labelStyle}>:</label>
              <input
                type="tel"
                id="phone_number"
                value={formData.phone_number}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="gender" style={labelStyle}>:</label>
              <select
                id="gender"
                value={formData.gender}
                onChange={handleChange}
                style={inputStyle}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="nationality" style={labelStyle}>:</label>
              <select
                id="nationality"
                value={formData.nationality}
                onChange={handleChange}
                style={inputStyle}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
          </div>
          
          {/*  */}
          <div>
            <h3></h3>
            
            <div style={formGroupStyle}>
              <label htmlFor="desired_occupation" style={labelStyle}>:</label>
              <input
                type="text"
                id="desired_occupation"
                value={formData.desired_occupation}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="desired_location" style={labelStyle}>:</label>
              <input
                type="text"
                id="desired_location"
                value={formData.desired_location}
                onChange={handleChange}
                style={inputStyle}
                placeholder=": "
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="employment_status" style={labelStyle}>:</label>
              <select
                id="employment_status"
                value={formData.employment_status}
                onChange={handleChange}
                style={inputStyle}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="available_date" style={labelStyle}>:</label>
              <select
                id="available_date"
                value={formData.available_date}
                onChange={handleChange}
                style={inputStyle}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="employment_period" style={labelStyle}>:</label>
              <select
                id="employment_period"
                value={formData.employment_period}
                onChange={handleChange}
                style={inputStyle}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="desired_working_hours" style={labelStyle}>:</label>
              <input
                type="text"
                id="desired_working_hours"
                value={formData.desired_working_hours}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="desired_salary" style={labelStyle}>:</label>
              <input
                type="text"
                id="desired_salary"
                value={formData.desired_salary}
                onChange={handleChange}
                style={inputStyle}
              />
            </div>
          </div>
        </div>
        
        {/*  */}
        <div style={{ marginTop: '20px' }}>
          <h3></h3>
          
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px' }}>
            <div>
              <div style={formGroupStyle}>
                <label htmlFor="medical_history" style={labelStyle}>:</label>
                <input
                  type="text"
                  id="medical_history"
                  value={formData.medical_history}
                  onChange={handleChange}
                  style={inputStyle}
                />
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="disability_certificate" style={labelStyle}>:</label>
                <select
                  id="disability_certificate"
                  value={formData.disability_certificate}
                  onChange={handleChange}
                  style={inputStyle}
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="tattoo" style={labelStyle}>:</label>
                <select
                  id="tattoo"
                  value={formData.tattoo}
                  onChange={handleChange}
                  style={inputStyle}
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              {formData.tattoo === '' && (
                <div style={formGroupStyle}>
                  <label htmlFor="tattoo_details" style={labelStyle}>:</label>
                  <input
                    type="text"
                    id="tattoo_details"
                    value={formData.tattoo_details}
                    onChange={handleChange}
                    style={inputStyle}
                  />
                </div>
              )}
              
              <div style={formGroupStyle}>
                <label htmlFor="criminal_record" style={labelStyle}>/:</label>
                <select
                  id="criminal_record"
                  value={formData.criminal_record}
                  onChange={handleChange}
                  style={inputStyle}
                >
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="clothing_size" style={labelStyle}>:</label>
                <select
                  id="clothing_size"
                  value={formData.clothing_size}
                  onChange={handleChange}
                  style={inputStyle}
                >
                  <option value=""></option>
                  <option value="S">S</option>
                  <option value="M">M</option>
                  <option value="L">L</option>
                  <option value="2L">2L</option>
                  <option value="3L">3L</option>
                  <option value="4L">4L</option>
                  <option value="5L">5L</option>
                </select>
              </div>
            </div>
            
            <div>
            <div style={formGroupStyle}>
                <label htmlFor="commute_or_dormitory" style={labelStyle}>/:</label>
                <select
                  id="commute_or_dormitory"
                  value={formData.commute_or_dormitory}
                  onChange={handleChange}
                  style={inputStyle}
                >
                <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="commute_method" style={labelStyle}>:</label>
                <select
                  id="commute_method"
                  value={formData.commute_method}
                  onChange={handleChange}
                  style={inputStyle}
                >
                <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="commute_area" style={labelStyle}>/:</label>
                <input
                  type="text"
                  id="commute_area"
                  value={formData.commute_area}
                  onChange={handleChange}
                  style={inputStyle}
                />
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="factory_experience" style={labelStyle}>:</label>
                <input
                  type="text"
                  id="factory_experience"
                  value={formData.factory_experience}
                  onChange={handleChange}
                  style={inputStyle}
                />
              </div>
              
              <div style={formGroupStyle}>
                <label htmlFor="experience_details" style={labelStyle}>:</label>
                <textarea
                  id="experience_details"
                  value={formData.experience_details}
                  onChange={handleChange}
                  style={{ ...inputStyle, height: '80px' }}
                />
              </div>
            </div>
          </div>
          
          <div style={formGroupStyle}>
            <label htmlFor="recent_applications" style={labelStyle}>:</label>
            <input
              type="text"
              id="recent_applications"
              value={formData.recent_applications}
              onChange={handleChange}
              style={inputStyle}
            />
          </div>
          
          <div style={formGroupStyle}>
            <label htmlFor="most_important_point" style={labelStyle}>:</label>
            <input
              type="text"
              id="most_important_point"
              value={formData.most_important_point}
              onChange={handleChange}
              style={inputStyle}
            />
          </div>
          
          <div style={formGroupStyle}>
            <label htmlFor="important_point_details" style={labelStyle}>:</label>
            <textarea
              id="important_point_details"
              value={formData.important_point_details}
              onChange={handleChange}
              style={{ ...inputStyle, height: '80px' }}
            />
          </div>
          
          <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px' }}>
            <div style={formGroupStyle}>
              <label htmlFor="height" style={labelStyle}>:</label>
              <input
                type="text"
                id="height"
                value={formData.height}
                onChange={handleChange}
                style={inputStyle}
                placeholder=": 170cm"
              />
            </div>
            
            <div style={formGroupStyle}>
              <label htmlFor="weight" style={labelStyle}>:</label>
              <input
                type="text"
                id="weight"
                value={formData.weight}
                onChange={handleChange}
                style={inputStyle}
                placeholder=": 65kg"
              />
            </div>
          </div>
        </div>
        
        <div style={{ marginTop: '20px', textAlign: 'center' }}>
          <button
            type="submit"
            style={buttonStyle}
            disabled={loading}
          >
            {loading ? '...' : ''}
          </button>
          <button
            type="button"
            onClick={() => navigate(`/applicants/${applicantId}`)}
            style={{
              padding: '10px 15px',
              backgroundColor: '#f44336',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              fontSize: '16px',
              marginLeft: '10px'
            }}
          >
            
          </button>
        </div>
      </form>
    </div>
  );
}

export default ApplicantEdit;
