import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

function EmployeesList() {
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortField, setSortField] = useState('name');
  const [sortDirection, setSortDirection] = useState('asc');
  const [showAddForm, setShowAddForm] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    department: '',
    position: '',
    email: '',
    phone_number: '',
    hire_date: ''
  });

  useEffect(() => {
    fetchEmployees();
  }, []);

  const fetchEmployees = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/employees');
      if (!response.ok) {
        throw new Error(`API error: ${response.status}`);
      }
      const data = await response.json();
      setEmployees(data);
      setLoading(false);
    } catch (error) {
      console.error('Error fetching employees:', error);
      setError(error.message);
      setLoading(false);
    }
  };

  const handleDelete = async (id) => {
    if (window.confirm('')) {
      try {
        const response = await fetch(`/api/employees/${id}`, {
          method: 'DELETE',
        });
        
        if (!response.ok) {
          throw new Error(`API error: ${response.status}`);
        }
        
        // 
        fetchEmployees();
      } catch (error) {
        console.error('Error deleting employee:', error);
        setError(error.message);
      }
    }
  };

  const handleSort = (field) => {
    // 
    if (field === sortField) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  const handleSearch = (e) => {
    setSearchTerm(e.target.value);
  };

  const handleChange = (e) => {
    const { id, value } = e.target;
    setFormData(prevData => ({
      ...prevData,
      [id]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.name) {
      alert('');
      return;
    }
    
    try {
      const response = await fetch('/api/employees', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || `API error: ${response.status}`);
      }
      
      // 
      setFormData({
        name: '',
        department: '',
        position: '',
        email: '',
        phone_number: '',
        hire_date: ''
      });
      setShowAddForm(false);
      fetchEmployees();
    } catch (error) {
      console.error('Error adding employee:', error);
      setError(error.message);
    }
  };

  // 
  const filteredAndSortedEmployees = employees
    .filter(employee => {
      // 
      return (
        employee.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        employee.department?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        employee.position?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        employee.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        employee.phone_number?.includes(searchTerm)
      );
    })
    .sort((a, b) => {
      // 
      if (!a[sortField]) return 1;
      if (!b[sortField]) return -1;
      
      const valueA = a[sortField].toString().toLowerCase();
      const valueB = b[sortField].toString().toLowerCase();
      
      if (sortDirection === 'asc') {
        return valueA.localeCompare(valueB);
      } else {
        return valueB.localeCompare(valueA);
      }
    });

  if (loading) {
    return <div>...</div>;
  }

  if (error) {
    return <div>: {error}</div>;
  }

  return (
    <div>
      <h2></h2>
      
      <div className="filters" style={{ marginBottom: '20px' }}>
        <input
          type="text"
          placeholder=""
          value={searchTerm}
          onChange={handleSearch}
          style={{ marginRight: '10px', padding: '5px' }}
        />
        
        <button 
          onClick={() => setShowAddForm(!showAddForm)}
          style={{ 
            padding: '8px 16px', 
            backgroundColor: showAddForm ? '#f44336' : '#4CAF50', 
            color: 'white', 
            border: 'none', 
            borderRadius: '4px', 
            cursor: 'pointer' 
          }}
        >
          {showAddForm ? '' : ''}
        </button>
      </div>
      
      {showAddForm && (
        <div style={{ marginBottom: '20px', padding: '15px', backgroundColor: '#f8f9fa', borderRadius: '8px' }}>
          <h3></h3>
          <form onSubmit={handleSubmit} style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '15px' }}>
            <div>
              <label htmlFor="name" style={{ display: 'block', marginBottom: '5px' }}>: <span style={{ color: 'red' }}>*</span></label>
              <input
                type="text"
                id="name"
                value={formData.name}
                onChange={handleChange}
                style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px' }}
                required
              />
            </div>
            
            <div>
              <label htmlFor="department" style={{ display: 'block', marginBottom: '5px' }}>:</label>
              <input
                type="text"
                id="department"
                value={formData.department}
                onChange={handleChange}
                style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px' }}
              />
            </div>
            
            <div>
              <label htmlFor="position" style={{ display: 'block', marginBottom: '5px' }}>:</label>
              <input
                type="text"
                id="position"
                value={formData.position}
                onChange={handleChange}
                style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px' }}
              />
            </div>
            
            <div>
              <label htmlFor="email" style={{ display: 'block', marginBottom: '5px' }}>:</label>
              <input
                type="email"
                id="email"
                value={formData.email}
                onChange={handleChange}
                style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px' }}
              />
            </div>
            
            <div>
              <label htmlFor="phone_number" style={{ display: 'block', marginBottom: '5px' }}>:</label>
              <input
                type="tel"
                id="phone_number"
                value={formData.phone_number}
                onChange={handleChange}
                style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px' }}
              />
            </div>
            
            <div>
              <label htmlFor="hire_date" style={{ display: 'block', marginBottom: '5px' }}>:</label>
              <input
                type="date"
                id="hire_date"
                value={formData.hire_date}
                onChange={handleChange}
                style={{ width: '100%', padding: '8px', border: '1px solid #ddd', borderRadius: '4px' }}
              />
            </div>
            
            <div style={{ gridColumn: 'span 2', textAlign: 'center', marginTop: '10px' }}>
              <button 
                type="submit" 
                style={{ 
                  padding: '10px 20px', 
                  backgroundColor: '#4CAF50', 
                  color: 'white', 
                  border: 'none', 
                  borderRadius: '4px', 
                  cursor: 'pointer',
                  fontSize: '16px'
                }}
              >
                
              </button>
            </div>
          </form>
        </div>
      )}
      
      <div style={{ overflowX: 'auto' }}>
        <table style={{ width: '100%', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ backgroundColor: '#f2f2f2' }}
>
              <th style={{ padding: '12px', textAlign: 'left', cursor: 'pointer' }} onClick={() => handleSort('name')}>
                 {sortField === 'name' && (sortDirection === 'asc' ? '' : '')}
              </th>
              <th style={{ padding: '12px', textAlign: 'left', cursor: 'pointer' }} onClick={() => handleSort('department')}>
                 {sortField === 'department' && (sortDirection === 'asc' ? '' : '')}
              </th>
  
            <th style={{ padding: '12px', textAlign: 'left', cursor: 'pointer' }} onClick={() => handleSort('position')}>
                 {sortField === 'position' && (sortDirection === 'asc' ? '' : '')}
              </th>
              <th style={{ padding: '12px', textAlign: 'left' }}></th>
              <th style={{ padding: '12px', textAlign: 'left', cursor: 'pointer' }} onClick={() => handleSort('hire_date')}>
                 {sortField === 'hire_date' && (sortDirection === 'asc' ? '' : '')}
              </th>
              <th style={{ padding: '12px', textAlign: 'center' }}>KPI</th>
              <th style={{ padding: '12px', textAlign: 'center' }}></th>
            </tr>
          </thead>
          <tbody>
            {filteredAndSortedEmployees.length > 0 ? (
              filteredAndSortedEmployees.map((employee) => (
                <tr key={employee.id} style={{ borderBottom: '1px solid #ddd' }}>
                  <td style={{ padding: '12px' }}>{employee.name}</td>
                  <td style={{ padding: '12px' }}>{employee.department}</td>
                  <td style={{ padding: '12px' }}>{employee.position}</td>
                  <td style={{ padding: '12px' }}>
                    {employee.email}<br />
                    {employee.phone_number}
                  </td>
                  <td style={{ padding: '12px' }}>{employee.hire_date}</td>
                  <td style={{ padding: '12px', textAlign: 'center' }}>
                    <Link to={`/employees/${employee.id}/kpi`} style={{
                      padding: '5px 10px',
                      backgroundColor: '#9C27B0',
                      color: 'white',
                      border: 'none',
                      borderRadius: '4px',
                      cursor: 'pointer',
                      textDecoration: 'none',
                      display: 'inline-block'
                    }}>
                      KPI
                    </Link>
                  </td>
                  <td style={{ padding: '12px', textAlign: 'center' }}>
                    <button 
                      onClick={() => alert('')}
                      style={{ 
                        marginRight: '5px', 
                        padding: '5px 10px', 
                        backgroundColor: '#2196F3', 
                        color: 'white', 
                        border: 'none', 
                        borderRadius: '4px', 
                        cursor: 'pointer' 
                      }}
                    >
                      
                    </button>
                    <button 
                      onClick={() => handleDelete(employee.id)}
                      style={{ 
                        padding: '5px 10px', 
                        backgroundColor: '#f44336', 
                        color: 'white', 
                        border: 'none', 
                        borderRadius: '4px', 
                        cursor: 'pointer' 
                      }}
                    >
                      
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="7" style={{ padding: '12px', textAlign: 'center' }}>
                  
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default EmployeesList;