// ApplicantMatching.js
import React, { useState, useEffect } from 'react';
import { useParams, useSearchParams, Link } from 'react-router-dom';
import axios from 'axios';

function ApplicantMatching() {
  const { applicantId } = useParams();
  const [searchParams] = useSearchParams();
  const applicantIdFromQuery = searchParams.get('applicantId');

  // URLID
  const currentApplicantId = applicantId || applicantIdFromQuery;

  const [applicant, setApplicant] = useState(null);
  const [matchingJobs, setMatchingJobs] = useState([]);
  const [processedJobs, setProcessedJobs] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const [sortBy, setSortBy] = useState('distance'); // 
  const [transportMode, setTransportMode] = useState('driving'); // 
  const [displayCount, setDisplayCount] = useState(20); // 

  useEffect(() => {
    // APIURL
    axios.defaults.baseURL = 'http://localhost:5001';

    // 
    const fetchApplicantData = async () => {
      try {
        setIsLoading(true);

        if (!currentApplicantId) {
          setError('ID');
          setIsLoading(false);
          return;
        }

        console.log(`: ${currentApplicantId}`);
        const applicantResponse = await axios.get(`/api/applicants/${currentApplicantId}`);

        if (applicantResponse.status !== 200) {
          throw new Error(`API error: ${applicantResponse.status}`);
        }

        const applicantData = applicantResponse.data;
        console.log(':', applicantData);

        // 
        console.log(':', {
          hasAddress: 'address' in applicantData,
          addressValue: applicantData.address,
          applicantKeys: Object.keys(applicantData)
        });

        // 
        setApplicant(applicantData);

        // 
        await fetchMatchingJobsWithApplicant(applicantData);
      } catch (error) {
        console.error('Error fetching data:', error);
        setError(`: ${error.message}`);
        setIsLoading(false);
      }
    };

    fetchApplicantData();
  }, [currentApplicantId]);

  // fetchMatchingJobs
  const fetchMatchingJobsWithApplicant = async (applicantData) => {
    try {
      const options = {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      };

      console.log(`: applicantId=${currentApplicantId}`);
      const response = await axios.get(
        `/api/matching/applicant/${currentApplicantId}`,
        options
      );

      if (response.status !== 200) {
        throw new Error(`API error: ${response.status}`);
      }

      console.log(':', response.data.length, '');
      setMatchingJobs(response.data);

      // 
      if (response.data.length > 0) {
        await calculateDistancesWithApplicant(response.data, applicantData);
      } else {
        setIsLoading(false);
      }
    } catch (error) {
      console.error('Error fetching matching jobs:', error);
      setError(`: ${error.message}`);
      setIsLoading(false);
    }
  };

  // calculateDistances
  const calculateDistancesWithApplicant = async (jobs, applicantData) => {
    try {
      // 
      if (!applicantData || !applicantData.address) {
        console.warn(':', applicantData);
        setError('');
        setProcessedJobs(jobs);
        setIsLoading(false);
        return;
      }

      // 
      let travelMode = transportMode;
      if (applicantData.commute_method) {
        if (applicantData.commute_method.includes('') || applicantData.commute_method.includes('')) {
          travelMode = 'driving';
        } else if (applicantData.commute_method.includes('') || applicantData.commute_method.includes('') || applicantData.commute_method.includes('')) {
          travelMode = 'transit';
        } else if (applicantData.commute_method.includes('')) {
          travelMode = 'bicycling';
        } else if (applicantData.commute_method.includes('')) {
          travelMode = 'walking';
        }
      }

      console.log(`: ${applicantData.commute_method || ''}, : ${travelMode}`);
      console.log(`: ${applicantData.address}`);

      // APIGoogle Distance Matrix API
      const destinations = jobs.map(job => `${job.job.prefecture}${job.job.city || ''}`);

      console.log(':', { 
        origin: applicantData.address, 
        destinations: destinations.slice(0, 3) + '...' 
      });

      //  - POST
      const distanceResponse = await axios.post('/api/calculate-distances', {
        origin: applicantData.address,
        destinations: destinations,
        mode: travelMode
      });

      if (distanceResponse.status !== 200) {
        throw new Error(`API error: ${distanceResponse.status}`);
      }

      console.log('');

      // 
      const jobsWithDistance = jobs.map((job, index) => {
        return {
          ...job,
          distance: distanceResponse.data.distances[index],
          duration: distanceResponse.data.durations[index],
          durationText: distanceResponse.data.durationTexts[index],
          distanceText: distanceResponse.data.distanceTexts[index]
        };
      });

      // 
      const sortedJobs = [...jobsWithDistance].sort((a, b) => {
        // nullduration
        if (a.duration === null && b.duration === null) return 0;
        if (a.duration === null) return 1;
        if (b.duration === null) return -1;
        return a.duration - b.duration;
      });

      // 
      const topJobs = sortedJobs.slice(0, displayCount);

      setProcessedJobs(topJobs);
    } catch (error) {
      console.error('Error calculating distances:', error);
      setError(`: ${error.message}`);
      setProcessedJobs(jobs); // 
    } finally {
      setIsLoading(false);
    }
  };

  // componentDidMount
  const fetchMatchingJobs = async () => {
    try {
      setIsLoading(true);

      // applicant
      if (!applicant) {
        console.warn('');
        setError('');
        setIsLoading(false);
        return;
      }

      const options = {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      };

      console.log(`: applicantId=${currentApplicantId}`);
      const response = await axios.get(
        `/api/matching/applicant/${currentApplicantId}`,
        options
      );

      if (response.status !== 200) {
        throw new Error(`API error: ${response.status}`);
      }

      console.log(':', response.data.length, '');
      setMatchingJobs(response.data);

      // 
      if (response.data.length > 0) {
        await calculateDistances(response.data);
      } else {
        setIsLoading(false);
      }
    } catch (error) {
      console.error('Error fetching matching jobs:', error);
      setError(`: ${error.message}`);
      setIsLoading(false);
    }
  };

  // Google Distance Matrix APIcomponentDidMount
  const calculateDistances = async (jobs) => {
    try {
      // applicant
      if (!applicant) {
        console.warn('');
        setError('');
        setProcessedJobs(jobs);
        setIsLoading(false);
        return;
      }

      if (!applicant.address) {
        console.warn('');
        setError('');
        setProcessedJobs(jobs);
        setIsLoading(false);
        return;
      }

      // 
      let travelMode = transportMode;
      if (applicant.commute_method) {
        if (applicant.commute_method.includes('') || applicant.commute_method.includes('')) {
          travelMode = 'driving';
        } else if (applicant.commute_method.includes('') || applicant.commute_method.includes('') || applicant.commute_method.includes('')) {
          travelMode = 'transit';
        } else if (applicant.commute_method.includes('')) {
          travelMode = 'bicycling';
        } else if (applicant.commute_method.includes('')) {
          travelMode = 'walking';
        }
      }

      console.log(`: ${applicant.commute_method || ''}, : ${travelMode}`);

      // APIGoogle Distance Matrix API
      const destinations = jobs.map(job => `${job.job.prefecture}${job.job.city || ''}`);

      //  - POST
      const distanceResponse = await axios.post('/api/calculate-distances', {
        origin: applicant.address,
        destinations: destinations,
        mode: travelMode
      });

      if (distanceResponse.status !== 200) {
        throw new Error(`API error: ${distanceResponse.status}`);
      }

      // 
      const jobsWithDistance = jobs.map((job, index) => {
        return {
          ...job,
          distance: distanceResponse.data.distances[index],
          duration: distanceResponse.data.durations[index],
          durationText: distanceResponse.data.durationTexts[index],
          distanceText: distanceResponse.data.distanceTexts[index]
        };
      });

      // 
      const sortedJobs = [...jobsWithDistance].sort((a, b) => {
        if (a.duration === null && b.duration === null) return 0;
        if (a.duration === null) return 1;
        if (b.duration === null) return -1;
        return a.duration - b.duration;
      });

      // 20
      const topJobs = sortedJobs.slice(0, displayCount);

      setProcessedJobs(topJobs);
    } catch (error) {
      console.error('Error calculating distances:', error);
      setError(`: ${error.message}`);
      setProcessedJobs(jobs); // 
    } finally {
      setIsLoading(false);
    }
  };

  // 
  const handleSortChange = (newSortBy) => {
    setSortBy(newSortBy);

    // 
    let sortedJobs = [...processedJobs];

    if (newSortBy === 'distance') {
      sortedJobs.sort((a, b) => {
        if (a.duration === null && b.duration === null) return 0;
        if (a.duration === null) return 1;
        if (b.duration === null) return -1;
        return a.duration - b.duration;
      });
    } else if (newSortBy === 'age_limit') {
      sortedJobs.sort((a, b) => {
        // 
        if (a.age_match && !b.age_match) return -1;
        if (!a.age_match && b.age_match) return 1;
        // 
        if (a.job.age_limit === null && b.job.age_limit !== null) return 1;
        if (a.job.age_limit !== null && b.job.age_limit === null) return -1;
        return (a.job.age_limit || 999) - (b.job.age_limit || 999);
      });
    } else if (newSortBy === 'location') {
      sortedJobs.sort((a, b) => b.location_match - a.location_match);
    }

    setProcessedJobs(sortedJobs);
  };

  // 
  const handleTransportModeChange = (newMode) => {
    setTransportMode(newMode);
    setIsLoading(true);

    // applicant
    if (applicant && matchingJobs.length > 0) {
      calculateDistancesWithApplicant(matchingJobs, applicant);
    } else {
      setIsLoading(false);
      setError('');
    }
  };

  // 
  const renderDebugInfo = () => {
    if (process.env.NODE_ENV !== 'development') return null;

    return (
      <div style={{ marginTop: '20px', padding: '10px', backgroundColor: '#f0f0f0', border: '1px solid #ddd', borderRadius: '4px', fontSize: '12px' }}>
        <h4>:</h4>
        <p>ID: {currentApplicantId}</p>
        <p>: {applicant ? JSON.stringify(applicant.address) : ''}</p>
        <p>: {applicant ? Object.keys(applicant).join(', ') : ''}</p>
        <button onClick={() => console.log(':', applicant)} style={{ padding: '5px 10px' }} >
          
        </button>
      </div>
    );
  };

  if (isLoading) {
    return <div style={{ padding: '20px', textAlign: 'center' }}>...</div>;
  }

  if (error) {
    return (
      <div style={{ padding: '20px' }}>
        <div style={{ padding: '10px', backgroundColor: '#f8d7da', color: '#721c24', borderRadius: '4px', margin: '10px 0' }}>
          : {error}
        </div>
        <Link to="/applicants" style={{ textDecoration: 'none', color: '#333' }}>
          <div style={{ display: 'flex', alignItems: 'center', marginTop: '15px' }}>
            <span style={{ marginRight: '5px' }}>&larr;</span> 
          </div>
        </Link>
      </div>
    );
  }

  if (!applicant) {
    return (
      <div style={{ padding: '20px' }}>
        <div></div>
        <Link to="/applicants" style={{ textDecoration: 'none', color: '#333' }}>
          <div style={{ display: 'flex', alignItems: 'center', marginTop: '15px' }}>
            <span style={{ marginRight: '5px' }}>&larr;</span> 
          </div>
        </Link>
      </div>
    );
  }

  return (
    <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px' }}>
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '20px' }}>
        <Link to={`/applicants/${currentApplicantId}`} style={{ textDecoration: 'none', color: '#333' }}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <span style={{ marginRight: '5px' }}>&larr;</span> 
          </div>
        </Link>
        <h2 style={{ margin: 0 }}>{applicant.name} </h2>
      </div>

      <div style={{ backgroundColor: '#f5f5f5', padding: '15px', borderRadius: '8px', marginBottom: '20px' }}>
        <div style={{ display: 'flex', flexWrap: 'wrap', gap: '10px', alignItems: 'center', justifyContent: 'space-between' }}>
          <div>
            <strong>:</strong> {applicant.address ? (
              <span>{applicant.address}</span>
            ) : (
              <span style={{ color: 'red' }}>
                
                <Link to={`/applicants/edit/${currentApplicantId}`} style={{ marginLeft: '10px', color: 'blue' }}>
                  
                </Link>
              </span>
            )}
            {applicant.commute_method && <span style={{ marginLeft: '15px' }}><strong>:</strong> {applicant.commute_method}</span>}
          </div>

          <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
            <div>
              <label htmlFor="transport-mode" style={{ marginRight: '8px' }}>:</label>
              <select
                id="transport-mode"
                value={transportMode}
                onChange={(e) => handleTransportModeChange(e.target.value)}
                style={{ padding: '5px', borderRadius: '4px' }}
              >
                <option value="driving"></option>
                <option value="transit"></option>
                <option value="bicycling"></option>
                <option value="walking"></option>
              </select>
            </div>

            <div>
              <label htmlFor="sort-select" style={{ marginRight: '8px' }}>:</label>
              <select
                id="sort-select"
                value={sortBy}
                onChange={(e) => handleSortChange(e.target.value)}
                style={{ padding: '5px', borderRadius: '4px' }}
              >
                <option value="distance"></option>
                <option value="age_limit"></option>
                <option value="location"></option>
              </select>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      {renderDebugInfo()}

      <div style={{ backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px', marginBottom: '20px' }}>
        <div style={{ marginBottom: '15px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
          <h3 style={{ margin: 0 }}>{displayCount}</h3>
        </div>

        {processedJobs.length > 0 ? (
          <div>
            {/*  */}
            {processedJobs[0].applicant_age && (
              <div style={{ marginBottom: '15px', padding: '10px', backgroundColor: '#e3f2fd', borderRadius: '4px' }}>
                <p style={{ margin: 0 }}>
                  <strong>:</strong> {processedJobs[0].applicant_age}
                  {sortBy === 'distance' && ' ()'}
                </p>
              </div>
            )}

            {/*  */}
            {processedJobs.map((result, index) => (
              <div key={index} style={{ marginBottom: '15px', padding: '15px', border: '1px solid #ddd', borderRadius: '4px', backgroundColor: '#fff' }}>
                <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '10px' }}>
                  <h4 style={{ margin: 0 }}>{result.job.title || result.job.company}</h4>

                  {/*  */}
                  <div style={{
                    padding: '3px 8px',
                    backgroundColor: '#e8f5e9',
                    color: '#2e7d32',
                    borderRadius: '4px',
                    fontSize: '0.9em',
                    display: 'flex',
                    alignItems: 'center'
                  }}>
                    <span>: {result.durationText || '...'}</span>
                  </div>
                </div>

                <div style={{ marginBottom: '10px', display: 'flex', flexWrap: 'wrap', gap: '8px' }}>
                  {/*  */}
                  <span style={{
                    padding: '2px 6px',
                    backgroundColor: result.age_match ? '#e8f5e9' : '#ffebee',
                    color: result.age_match ? '#2e7d32' : '#c62828',
                    borderRadius: '4px',
                    fontSize: '0.85em'
                  }}>
                    {result.age_match ? 'OK' : ''}
                    {result.job.age_limit && ` (${result.job.age_limit})`}
                  </span>

                  {/*  */}
                  <span style={{
                    padding: '2px 6px',
                    backgroundColor: '#f0f4c3',
                    borderRadius: '4px',
                    fontSize: '0.85em'
                  }}>
                    : {result.distanceText || '...'}
                  </span>

                  {/*  */}
                  {result.location_match > 0 && (
                    <span style={{
                      padding: '2px 6px',
                      backgroundColor: '#e3f2fd',
                      borderRadius: '4px',
                      fontSize: '0.85em'
                    }}>
                      : {result.location_match === 100 ? '' : ''}
                    </span>
                  )}
                </div>

                <p><strong>:</strong> {result.job.company}</p>
                <p><strong>:</strong> {result.job.prefecture} {result.job.city || ''}</p>
                <p><strong>:</strong> {result.job.salary || ''}</p>

                <div style={{ textAlign: 'right' }}>
                  <Link to={`/jobs/${result.job.id}`}>
                    <button style={{
                      padding: '5px 10px',
                      backgroundColor: '#2196F3',
                      color: 'white',
                      border: 'none',
                      borderRadius: '4px',
                      cursor: 'pointer'
                    }}>
                      
                    </button>
                  </Link>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div style={{ textAlign: 'center', padding: '20px', backgroundColor: '#f5f5f5', borderRadius: '4px' }}>
            <p></p>
          </div>
        )}
      </div>
    </div>
  );
}

export default ApplicantMatching;
