import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';

function ApplicantDetail() {
  const [applicant, setApplicant] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState('');
  const [activeTab, setActiveTab] = useState('basic'); // 
  const [progress, setProgress] = useState({
    application_date: null,
    call_date: null,
    connection_date: null,
    proposal_date: null,
    document_sent_date: null,
    document_passed_date: null,
    interview_date: null,
    offer_date: null,
    hire_date: null,
    payment_date: null
  });
  const [referralFee, setReferralFee] = useState('');
  const { applicantId } = useParams();
  const navigate = useNavigate();

  // 
  const [employees, setEmployees] = useState([]);
  // ID
  const [assignedEmployeeId, setAssignedEmployeeId] = useState('');
  // 
  const [matchingJobs, setMatchingJobs] = useState([]);
  const [isLoadingMatches, setIsLoadingMatches] = useState(false);
  const [matchingError, setMatchingError] = useState(null);
  const [sortBy, setSortBy] = useState('age_limit'); // 

  useEffect(() => {
    // Axios
     // baseURL
    axios.defaults.baseURL = '';  // 
    axios.defaults.headers.common['Content-Type'] = 'application/json';
    axios.defaults.headers.common['Accept'] = 'application/json';

    // 
    const fetchData = async () => {
      try {
        setLoading(true);
        console.log('');

        // 
        const applicantResponse = await axios.get(`/api/applicants/${applicantId}`);
        if (applicantResponse.status !== 200) {
          throw new Error(`API error: ${applicantResponse.status}`);
        }

        const applicantData = applicantResponse.data;
        
        // 
        console.log(':', applicantData);
        console.log(':', {
          employee_id: applicantData.employee_id,
          assigned_employee_id: applicantData.assigned_employee_id,
          dataType: typeof(applicantData.employee_id || applicantData.assigned_employee_id)
        });
        
        setApplicant(applicantData);

        // 
        if (applicantData.progress) {
          setProgress(applicantData.progress);
        } else {
          // 
          const progressData = {};
          ['application_date', 'call_date', 'connection_date', 'proposal_date',
           'document_sent_date', 'document_passed_date', 'interview_date',
           'offer_date', 'hire_date', 'payment_date'].forEach(field => {
            progressData[field] = applicantData[field] || null;
          });
          setProgress(progressData);
        }

        // 
        if (applicantData.referral_fee) {
          setReferralFee(applicantData.referral_fee);
        }

        // ID - 
        if (applicantData.assigned_employee_id) {
          console.log('assigned_employee_id:', applicantData.assigned_employee_id);
          // 
          setAssignedEmployeeId(String(applicantData.assigned_employee_id));
        } else if (applicantData.employee_id) {
          console.log('employee_id:', applicantData.employee_id);
          // 
          setAssignedEmployeeId(String(applicantData.employee_id));
        } else {
          console.log('ID');
          setAssignedEmployeeId('');
        }

        // 
        const employeesResponse = await axios.get('/api/employees');
        if (employeesResponse.status === 200) {
          setEmployees(employeesResponse.data);
          console.log(':', employeesResponse.data);
        }

        setLoading(false);
      } catch (error) {
        console.error('Error fetching data:', error);
        setError(`: ${error.message}`);
        setLoading(false);
      }
    };

    fetchData();
  }, [applicantId]);

  // applicantIDuseEffect
  useEffect(() => {
    if (applicant) {
      const employeeId = applicant.employee_id || applicant.assigned_employee_id;
      if (employeeId !== undefined && employeeId !== null) {
        console.log('applicantID:', employeeId);
        setAssignedEmployeeId(String(employeeId));
      }
    }
  }, [applicant]);

  // 
  const formatDateForAPI = (dateString) => {
    if (!dateString) return null;
    try {
      const date = new Date(dateString);
      if (isNaN(date.getTime())) return null; // 
      return date.toISOString().split('T')[0]; // YYYY-MM-DD
    } catch (e) {
      console.error(':', e);
      return null;
    }
  };

  // 
  const handleProgressChange = async (field, value) => {
    try {
      console.log('applicantId:', applicantId);
      console.log(':', field, ':', value);
      // 
      const formattedValue = field.endsWith('_date') ? formatDateForAPI(value) : value;
      const updatedProgress = { ...progress, [field]: formattedValue };
      setProgress(updatedProgress);

      // 
      const options = {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        withCredentials: false
      };

      console.log(':', `/api/applicants/${applicantId}/progress`);
      console.log(':', updatedProgress);
      // API
      const progressResponse = await axios.put(
        `/api/applicants/${applicantId}/progress`,
        updatedProgress,
        options
      );

      // 
      console.log('API:', progressResponse);
      if (progressResponse.status !== 200) {
        throw new Error(`API error: ${progressResponse.status}`);
      }
      showSuccessMessage('');
    } catch (error) {
      console.error('Error updating progress:', error);
      // 
      let errorMessage = `: ${error.message}`;
      // 
      if (error.response) {
        console.log('Error response:', error.response);
        errorMessage += ` (Status: ${error.response.status})`;
        if (error.response.data && error.response.data.error) {
          errorMessage += ` - ${error.response.data.error}`;
        }
      } else if (error.request) {
        // 
        console.log('Error request:', error.request);
        errorMessage += ' - ';
      }
      setError(errorMessage);
    }
  };

  const handleEmployeeChange = async (employeeId) => {
    try {
      console.log(':', employeeId, typeof employeeId);
      
      // IDAPI
      setAssignedEmployeeId(employeeId);
      
      // employee_id
      const requestData = {
        employee_id: employeeId  // assigned_employee_id  employee_id
      };
      
      console.log(':', requestData);
      
      const response = await axios.put(
        `/api/applicants/${applicantId}/assign-employee`,
        requestData,
        {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        }
      );
      
      if (response.status !== 200) {
        throw new Error(`API error: ${response.status}`);
      }
      
      console.log('API:', response.data);
      showSuccessMessage('');
      
      // 
      setApplicant(prev => ({
        ...prev,
        employee_id: employeeId,
        assigned_employee_id: employeeId
      }));
      
    } catch (error) {
      console.error('Error updating assigned employee:', error);
      
      // 
      if (applicant && (applicant.employee_id || applicant.assigned_employee_id)) {
        setAssignedEmployeeId(String(applicant.employee_id || applicant.assigned_employee_id));
      } else {
        setAssignedEmployeeId('');
      }
      
      let errorMessage = `: ${error.message}`;
      
      if (error.response) {
        errorMessage += ` (Status: ${error.response.status})`;
        if (error.response.data && error.response.data.error) {
          errorMessage += ` - ${error.response.data.error}`;
        }
      }
      
      setError(errorMessage);
    }
  };

  // 
  const fetchApplicantData = async () => {
    try {
      console.log('');
      const response = await axios.get(`/api/applicants/${applicantId}`);
      
      if (response.status !== 200) {
        throw new Error(`API error: ${response.status}`);
      }
      
      const applicantData = response.data;
      console.log(':', applicantData);
      setApplicant(applicantData);
      
      // ID
      if (applicantData.assigned_employee_id) {
        setAssignedEmployeeId(String(applicantData.assigned_employee_id));
      } else if (applicantData.employee_id) {
        setAssignedEmployeeId(String(applicantData.employee_id));
      }
    } catch (error) {
      console.error('Error re-fetching applicant data:', error);
      setError(`: ${error.message}`);
    }
  };

  // 
  const handleReferralFeeChange = (e) => {
    // 
    const value = e.target.value.replace(/[^\d]/g, '');
    setReferralFee(value);
  };

  // 
  const saveReferralFee = async () => {
    try {
      const options = {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        withCredentials: false
      };

      console.log(':', { referral_fee: referralFee });
      const referralFeeResponse = await axios.put(
        `/api/applicants/${applicantId}/referral-fee`,
        { referral_fee: referralFee },
        options
      );

      if (referralFeeResponse.status !== 200) {
        throw new Error(`API error: ${referralFeeResponse.status}`);
      }
      showSuccessMessage('');
    } catch (error) {
      console.error('Error saving referral fee:', error);
      let errorMessage = `: ${error.message}`;
      if (error.response) {
        errorMessage += ` (Status: ${error.response.status})`;
      } else if (error.request) {
        errorMessage += ' - ';
      }
      setError(errorMessage);
    }
  };

  // 
  const fetchMatchingJobs = async (sort = sortBy) => {
    try {
      setIsLoadingMatches(true);
      setMatchingError(null);
      const options = {
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      };

      console.log(`: applicantId=${applicantId}, sort=${sort}`);
      const response = await axios.get(
        `/api/matching/applicant/${applicantId}?sort_by=${sort}`,
        options
      );

      if (response.status !== 200) {
        throw new Error(`API error: ${response.status}`);
      }
      console.log(':', response.data.length, '');
      setMatchingJobs(response.data);
      setIsLoadingMatches(false);
    } catch (error) {
      console.error('Error fetching matching jobs:', error);
      setMatchingError(`: ${error.message}`);
      setIsLoadingMatches(false);
    }
  };

  // 
  const handleSortChange = (newSortBy) => {
    console.log(':', newSortBy);
    setSortBy(newSortBy);
    fetchMatchingJobs(newSortBy);
  };

  // 
  const showSuccessMessage = (message) => {
    setSuccessMessage(message);
    // 
    setError(null);
    setTimeout(() => setSuccessMessage(''), 3000);
  };

  if (loading) {
    return <div>...</div>;
  }

  if (error) {
    return (
      <div>
        <div style={{
          padding: '10px',
          backgroundColor: '#f8d7da',
          color: '#721c24',
          borderRadius: '4px',
          margin: '10px 0'
        }}>
          : {error}
        </div>
        <button
          onClick={() => setError(null)}
          style={{
            padding: '8px 16px',
            backgroundColor: '#6c757d',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
      </div>
    );
  }

  if (!applicant) {
    return <div></div>;
  }

  return (
    <div style={{ maxWidth: '1200px', margin: '0 auto', padding: '20px' }}>
      {/*  */}
      {successMessage && (
        <div style={{
          padding: '10px',
          backgroundColor: '#4CAF50',
          color: 'white',
          borderRadius: '4px',
          margin: '10px 0'
        }}>
          {successMessage}
        </div>
      )}

      {/*  */}
      {error && (
        <div style={{
          padding: '10px',
          backgroundColor: '#f8d7da',
          color: '#721c24',
          borderRadius: '4px',
          margin: '10px 0'
        }}>
          {error}
          <button
            onClick={() => setError(null)}
            style={{
              marginLeft: '10px',
              padding: '2px 8px',
              backgroundColor: 'transparent',
              color: '#721c24',
              border: '1px solid #721c24',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
        </div>
      )}

      <div style={{ marginBottom: '20px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Link to="/applicants" style={{ textDecoration: 'none', color: '#333' }}>
          <div style={{ display: 'flex', alignItems: 'center' }}>
            <span style={{ marginRight: '5px' }}>&larr;</span> 
          </div>
        </Link>
        <h2 style={{ margin: 0 }}>{applicant.name} </h2>
      </div>

      {/*  */}
      <div style={{ display: 'flex', marginBottom: '30px', gap: '25px', flexWrap: 'wrap' }}>
        {/*  */}
        <div style={{
          flex: '3',
          minWidth: '650px',
          backgroundColor: '#f9f9f9',
          padding: '20px',
          borderRadius: '8px'
        }}>
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '15px' }}>
            <h3 style={{ margin: 0 }}></h3>
            {/*  -  */}
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <label htmlFor="assigned-employee" style={{ marginRight: '10px', fontWeight: 'bold' }}>
                :
              </label>
              <select
                id="assigned-employee"
                value={assignedEmployeeId || ''}
                onChange={(e) => handleEmployeeChange(e.target.value)}
                style={{
                  padding: '8px',
                  borderRadius: '4px',
                  border: '1px solid #ddd',
                  backgroundColor: '#fff',
                  minWidth: '150px'
                }}
              >
                <option value=""></option>
                {employees.map(employee => {
                  // 
                  const employeeIdStr = String(employee.id);
                  const isSelected = employeeIdStr === assignedEmployeeId;
                  
                  return (
                    <option 
                      key={employeeIdStr} 
                      value={employeeIdStr}
                    >
                      {employee.name} {employee.department ? `(${employee.department})` : ''}
                      {isSelected ? ' ' : ''}
                    </option>
                  );
                })}
              </select>
              {/*  
              {process.env.NODE_ENV !== 'production' && (
                <div style={{ 
                  fontSize: '12px', 
                  color: '#666', 
                  marginLeft: '10px',
                }}>
                  ID: {assignedEmployeeId || ''}
                </div>*/}
              
            </div>
          </div>

          <div style={{
            display: 'flex',
            flexWrap: 'wrap',
            gap: '10px',
            justifyContent: 'space-between'
          }}>
            {[
              { label: '', field: 'application_date' },
              { label: '', field: 'call_date' },
              { label: '', field: 'connection_date' },
              { label: '', field: 'proposal_date' },
              { label: 'F', field: 'document_sent_date' },
              { label: 'F', field: 'document_passed_date' },
              { label: '', field: 'interview_date' },
              { label: '', field: 'offer_date' },
              { label: '', field: 'hire_date' },
              { label: '', field: 'payment_date' }
            ].map((step, index, arr) => {
              const hasDate = progress[step.field] !== null && progress[step.field] !== undefined && progress[step.field] !== '';
              const isCompleted = hasDate;
              return (
                <div
                  key={step.field}
                  style={{
                    width: 'calc(10% - 10px)',
                    textAlign: 'center',
                    position: 'relative'
                  }}
                >
                  {/*  */}
                  {index < arr.length - 1 && (
                    <div style={{
                      position: 'absolute',
                      top: '12px',
                      right: '-50%',
                      width: '100%',
                      height: '2px',
                      backgroundColor: isCompleted ? '#4CAF50' : '#ddd',
                      zIndex: 1
                    }}></div>
                  )}
                  {/*  */}
                  <div style={{
                    width: '25px',
                    height: '25px',
                    borderRadius: '50%',
                    backgroundColor: isCompleted ? '#4CAF50' : '#ddd',
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    margin: '0 auto',
                    color: 'white',
                    fontWeight: 'bold',
                    position: 'relative',
                    zIndex: 2,
                    fontSize: '12px'
                  }}>
                    {index + 1}
                  </div>
                  {/*  */}
                  <div style={{ marginTop: '5px', fontSize: '12px' }}>
                    {step.label}
                  </div>
                  {/*  */}
                  <input
                    type="date"
                    value={progress[step.field] || ''}
                    onChange={(e) => handleProgressChange(step.field, e.target.value)}
                    style={{
                      marginTop: '5px',
                      padding: '3px',
                      width: '100%',
                      fontSize: '10px'
                    }}
                  />
                </div>
              );
            })}
          </div>
        </div>

        {/*  */}
        <div style={{
          flex: '1',
          minWidth: '250px',
          backgroundColor: '#f9f9f9',
          padding: '20px',
          borderRadius: '8px'
        }}>
          <h3 style={{ marginTop: 0 }}></h3>
          <div style={{ marginBottom: '10px' }}>
            <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>
              :
            </label>
            <div style={{ display: 'flex' }}>
              <span style={{
                display: 'flex',
                alignItems: 'center',
                padding: '0 10px',
                backgroundColor: '#f0f0f0',
                borderRadius: '4px 0 0 4px'
              }}></span>
              <input
                value={referralFee}
                onChange={handleReferralFeeChange}
                style={{
                  flex: 1,
                  padding: '8px',
                  border: '1px solid #ddd',
                  borderRadius: '0 4px 4px 0',
                  borderLeft: 'none'
                }}
                placeholder=": 300000"
              />
            </div>
          </div>
          <button
            onClick={saveReferralFee}
            style={{
              padding: '8px 16px',
              backgroundColor: '#2196F3',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer',
              width: '100%'
            }}
          >
            
          </button>
        </div>
      </div>

      {/*  -  */}
      <div style={{
        display: 'flex',
        borderBottom: '1px solid #ddd',
        marginBottom: '15px',
        overflow: 'auto'
      }}>
        {['basic', 'work', 'physical', 'other', 'matching'].map(tab => {
          const labels = {
            'basic': '',
            'work': '',
            'physical': '',
            'other': '',
            'matching': ''
          };
          return (
            <button
              key={tab}
              onClick={() => {
                setActiveTab(tab);
                // 
                if (tab === 'matching' && matchingJobs.length === 0) {
                  fetchMatchingJobs();
                }
              }}
              style={{
                padding: '10px 20px',
                backgroundColor: activeTab === tab ? '#f5f5f5' : 'transparent',
                border: 'none',
                borderBottom: activeTab === tab ? '2px solid #2196F3' : 'none',
                cursor: 'pointer',
                fontWeight: activeTab === tab ? 'bold' : 'normal',
                color: activeTab === tab ? '#2196F3' : '#333'
              }}
            >
              {labels[tab]}
            </button>
          );
        })}
      </div>

      {/*  */}
      <div style={{ backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px', marginBottom: '20px' }}>
        {activeTab === 'basic' && (
          <div className="tab-content">
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))',
              gap: '20px'
            }}>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.name}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.birthdate || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.address || ''}</span>
                </div>
              </div>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.email || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.phone_number || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"> / </span>
                  <span className="info-value">{applicant.gender || ''} / {applicant.nationality || ''}</span>
                </div>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'work' && (
          <div className="tab-content">
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))',
              gap: '20px'
            }}>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.desired_occupation || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.desired_location || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.employment_status || ''}</span>
                </div>
              </div>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.available_date || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.employment_period || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.desired_salary || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.desired_working_hours || ''}</span>
                </div>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'physical' && (
          <div className="tab-content">
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))',
              gap: '20px'
            }}>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.height || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.weight || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.clothing_size || ''}</span>
                </div>
              </div>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.medical_history || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.disability_certificate || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">
                    {applicant.tattoo || ''}
                    {applicant.tattoo === '' && applicant.tattoo_details ? `${applicant.tattoo_details}` : ''}
                  </span>
                </div>
                <div className="info-item">
                  <span className="info-label">/</span>
                  <span className="info-value">{applicant.criminal_record || ''}</span>
                </div>
              </div>
            </div>
          </div>
        )}

        {activeTab === 'other' && (
          <div className="tab-content">
            <div style={{
              display: 'grid',
              gridTemplateColumns: 'repeat(auto-fit, minmax(280px, 1fr))',
              gap: '20px'
            }}>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label">/</span>
                  <span className="info-value">{applicant.commute_or_dormitory || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.commute_method || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label">/</span>
                  <span className="info-value">{applicant.commute_area || ''}</span>
                </div>
              </div>
              <div className="info-group">
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.factory_experience || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.most_important_point || ''}</span>
                </div>
                <div className="info-item">
                  <span className="info-label"></span>
                  <span className="info-value">{applicant.recent_applications || ''}</span>
                </div>
              </div>
            </div>

            {(applicant.experience_details || applicant.important_point_details) && (
              <div style={{ marginTop: '15px' }}>
                {applicant.experience_details && (
                  <div className="info-item" style={{ marginBottom: '15px' }}>
                    <span className="info-label"></span>
                    <span className="info-value" style={{ whiteSpace: 'pre-wrap', display: 'block', padding: '10px', backgroundColor: '#fff', borderRadius: '4px' }}>
                      {applicant.experience_details}
                    </span>
                  </div>
                )}

                {applicant.important_point_details && (
                  <div className="info-item">
                    <span className="info-label"></span>
                    <span className="info-value" style={{ whiteSpace: 'pre-wrap', display: 'block', padding: '10px', backgroundColor: '#fff', borderRadius: '4px' }}>
                      {applicant.important_point_details}
                    </span>
                  </div>
                )}
              </div>
            )}
          </div>
        )}

        {/*  (API) */}
        {activeTab === 'matching' && (
          <div className="tab-content">
            <div style={{ marginBottom: '15px', display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
              <h3 style={{ margin: 0 }}></h3>
              <div>
                <label htmlFor="sort-select" style={{ marginRight: '8px' }}>:</label>
                <select
                  id="sort-select"
                  value={sortBy}
                  onChange={(e) => handleSortChange(e.target.value)}
                  style={{ padding: '5px', borderRadius: '4px' }}
                >
                  <option value="age_limit"></option>
                  <option value="location"></option>
                </select>
              </div>
            </div>

            {isLoadingMatches ? (
              <div style={{ textAlign: 'center', padding: '20px' }}>
                <p>...</p>
              </div>
            ) : matchingError ? (
              <div style={{ color: 'red', padding: '10px', backgroundColor: '#ffebee', borderRadius: '4px' }}>
                <p>{matchingError}</p>
              </div>
            ) : matchingJobs.length > 0 ? (
              <div>
                {/*  */}
                {matchingJobs[0].applicant_age && (
                  <div style={{
                    marginBottom: '15px',
                    padding: '10px',
                    backgroundColor: '#e3f2fd',
                    borderRadius: '4px'
                  }}>
                    <p style={{ margin: 0 }}>
                      <strong>:</strong> {matchingJobs[0].applicant_age}
                      {sortBy === 'age_limit' && ' ()'}
                      {sortBy === 'location' && ' ()'}
                    </p>
                  </div>
                )}

                {/*  */}
                {matchingJobs.map((result, index) => (
                  <div key={index} style={{
                    marginBottom: '15px',
                    padding: '15px',
                    border: '1px solid #ddd',
                    borderRadius: '4px',
                    backgroundColor: '#fff'
                  }}>
                    <div style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '10px' }}>
                      <h4 style={{ margin: 0 }}>{result.job.title || result.job.company}</h4>
                      {/*  */}
                      <div style={{
                        padding: '3px 8px',
                        backgroundColor: result.age_match ? '#e8f5e9' : '#ffebee',
                        color: result.age_match ? '#2e7d32' : '#c62828',
                        borderRadius: '4px',
                        fontSize: '0.9em',
                        display: 'flex',
                        alignItems: 'center'
                      }}>
                        {result.age_match
                          ? <span>OK</span>
                          : <span></span>}
                      </div>
                    </div>

                    <div style={{ marginBottom: '10px', display: 'flex', flexWrap: 'wrap', gap: '8px' }}>
                      {/*  */}
                      {result.job.age_limit && (
                        <span style={{
                          padding: '2px 6px',
                          backgroundColor: '#f5f5f5',
                          borderRadius: '4px',
                          fontSize: '0.85em'
                        }}>
                          : {result.job.age_limit}
                        </span>
                      )}

                      {/*  */}
                      {result.location_match > 0 && (
                        <span style={{
                          padding: '2px 6px',
                          backgroundColor: '#e3f2fd',
                          borderRadius: '4px',
                          fontSize: '0.85em'
                        }}>
                          : {result.location_match === 100 ? '' : ''}
                        </span>
                      )}
                    </div>

                    <p><strong>:</strong> {result.job.company}</p>
                    <p><strong>:</strong> {result.job.prefecture} {result.job.city || ''}</p>
                    <p><strong>:</strong> {result.job.salary || ''}</p>
                    <div style={{ textAlign: 'right' }}>
                      <Link to={`/jobs/${result.job.id}`}>
                        <button style={{
                          padding: '5px 10px',
                          backgroundColor: '#2196F3',
                          color: 'white',
                          border: 'none',
                          borderRadius: '4px',
                          cursor: 'pointer'
                        }}>
                          
                        </button>
                      </Link>
                    </div>
                  </div>
                ))}
              </div>
            ) : (
              <div style={{ textAlign: 'center', padding: '20px', backgroundColor: '#f5f5f5', borderRadius: '4px' }}>
                <p></p>
              </div>
            )}
          </div>
        )}
      </div>

      {/*  */}
      <div style={{ marginTop: '30px', display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
        <Link to={`/applicants/${applicant.id}/matching`}>
          <button
            style={{
              padding: '10px 20px',
              backgroundColor: '#4CAF50',
              color: 'white',
              border: 'none',
              borderRadius: '4px',
              cursor: 'pointer'
            }}
          >
            
          </button>
        </Link>
        <button
          onClick={() => navigate(`/interviews/add?applicantId=${applicant.id}`)}
          style={{
            padding: '10px 20px',
            backgroundColor: '#2196F3',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
        <button
          onClick={() => navigate(`/phone-calls/add?applicantId=${applicant.id}`)}
          style={{
            padding: '10px 20px',
            backgroundColor: '#FF9800',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
        <button
          onClick={() => navigate(`/applicants/edit/${applicant.id}`)}
          style={{
            padding: '10px 20px',
            backgroundColor: '#9E9E9E',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
      </div>

      {/* CSS */}
      <style>{`
        .tab-content {
          animation: fadeIn 0.3s ease;
        }
        .info-group {
          margin-bottom: 15px;
        }
        .info-item {
          margin-bottom: 10px;
        }
        .info-label {
          font-size: 12px;
          color: #666;
          display: block;
          margin-bottom: 2px;
        }
        .info-value {
          font-size: 15px;
        }
        @keyframes fadeIn {
          from { opacity: 0; }
          to { opacity: 1; }
        }
        @media (max-width: 768px) {
          button {
            padding: 8px 12px;
            font-size: 14px;
          }
        }
      `}</style>
    </div>
  );
}

export default ApplicantDetail;
