import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';

function JobDetail() {
  const [job, setJob] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { jobId } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    // 
    const fetchJobDetail = async () => {
      try {
        setLoading(true);
        console.log(`JobDetail: Fetching data for job ID: ${jobId}`);
        
        // API
        const response = await fetch(`/api/jobs/${jobId}`);
        
        // 
        console.log(`API Response Status: ${response.status}`);
        
        if (!response.ok) {
          throw new Error(`API error: ${response.status}`);
        }

        const data = await response.json();
        console.log('JobDetail: Successfully retrieved job data:', data);
        
        setJob(data);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching job details:', error);
        setError(error.message);
        setLoading(false);
      }
    };

    fetchJobDetail();
  }, [jobId]);

  // 
  if (loading) {
    return <div>...</div>;
  }

  // 
  if (error) {
    return (
      <div style={{ padding: '20px', backgroundColor: '#f8d7da', color: '#721c24', borderRadius: '5px', margin: '20px 0' }}>
        <h3></h3>
        <p>{error}</p>
        <button 
          onClick={() => navigate('/jobs')}
          style={{ 
            padding: '8px 16px', 
            backgroundColor: '#6c757d', 
            color: 'white', 
            border: 'none', 
            borderRadius: '4px', 
            cursor: 'pointer' 
          }}
        >
          
        </button>
      </div>
    );
  }

  // 
  if (!job) {
    return (
      <div style={{ padding: '20px', backgroundColor: '#fff3cd', color: '#856404', borderRadius: '5px', margin: '20px 0' }}>
        <h3></h3>
        <p>ID: {jobId}</p>
        <button 
          onClick={() => navigate('/jobs')}
          style={{ 
            padding: '8px 16px', 
            backgroundColor: '#6c757d', 
            color: 'white', 
            border: 'none', 
            borderRadius: '4px', 
            cursor: 'pointer' 
          }}
        >
          
        </button>
      </div>
    );
  }

  return (
    <div>
      <div style={{ marginBottom: '20px' }}>
        <Link to="/jobs">&larr; </Link>
      </div>

      <h2>{job.title || job.company || ''}</h2>
      
      {/*  */}
      {/*<div style={{ backgroundColor: '#f0f0f0', padding: '10px', borderRadius: '5px', marginBottom: '15px', fontSize: '12px' }}>
        <p>ID: {job.id}</p>
      </div>*/}

      <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '20px' }}>
        {/*  */}
        <div style={{ backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
          <h3></h3>
          <table style={{ width: '100%' }}>
            <tbody>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.company || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>
                  {job.prefecture && job.city 
                    ? `${job.prefecture} ${job.city}`
                    : job.prefecture 
                      ? job.prefecture
                      : job.city
                        ? job.city
                        : ''
                  }
                </td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.salary || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.fee || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>
                  {job.min_age && job.age_limit 
                    ? `${job.min_age}${job.age_limit}`
                    : job.age_limit 
                      ? `${job.age_limit}`
                      : job.min_age
                        ? `${job.min_age}`
                        : ''
                  }
                </td>
              </tr>
              {job.job_number && (
                <tr>
                  <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                  <td>{job.job_number}</td>
                </tr>
              )}
              {job.cf_fc && (
                <tr>
                  <th style={{ textAlign: 'left', padding: '8px 0' }}>CF/FC</th>
                  <td>{job.cf_fc}</td>
                </tr>
              )}
            </tbody>
          </table>
        </div>

        {/*  */}
        <div style={{ backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
          <h3></h3>
          <table style={{ width: '100%' }}>
            <tbody>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.work_style || job.employment_type || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.holidays || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.annual_holidays || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.shift || ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.hourly_wage ? `${job.hourly_wage}` : job.salary_type === '' ? job.salary : ''}</td>
              </tr>
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0' }}></th>
                <td>{job.dormitory === true ? '' : job.dormitory === false ? '' : ''}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      {/*  */}
      {(job.description || job.requirements || job.benefits) && (
        <div style={{ marginTop: '20px' }}>
          {job.description && (
            <div style={{ marginTop: '20px', backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
              <h3></h3>
              <p style={{ whiteSpace: 'pre-wrap' }}>{job.description}</p>
            </div>
          )}

          {job.requirements && (
            <div style={{ marginTop: '20px', backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
              <h3></h3>
              <p style={{ whiteSpace: 'pre-wrap' }}>{job.requirements}</p>
            </div>
          )}

          {job.benefits && (
            <div style={{ marginTop: '20px', backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
              <h3></h3>
              <p style={{ whiteSpace: 'pre-wrap' }}>{job.benefits}</p>
            </div>
          )}

          {job.advantages && (
            <div style={{ marginTop: '20px', backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
              <h3></h3>
              <p style={{ whiteSpace: 'pre-wrap' }}>{job.advantages}</p>
            </div>
          )}
        </div>
      )}

      {/*  */}
      <div style={{ marginTop: '20px', backgroundColor: '#f9f9f9', padding: '20px', borderRadius: '8px' }}>
        <h3></h3>
        <table style={{ width: '100%' }}>
          <tbody>
            {job.products && (
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0', width: '30%' }}></th>
                <td>{job.products}</td>
              </tr>
            )}
            {job.occupation_major_category && (
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0', width: '30%' }}></th>
                <td>{job.occupation_major_category}</td>
              </tr>
            )}
            {job.occupation_minor_category && (
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0', width: '30%' }}></th>
                <td>{job.occupation_minor_category}</td>
              </tr>
            )}
            {job.commute_method && (
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0', width: '30%' }}></th>
                <td>{job.commute_method}</td>
              </tr>
            )}
            {job.nearest_station && (
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0', width: '30%' }}></th>
                <td>{job.nearest_station}</td>
              </tr>
            )}
            {job.smoking_measures && (
              <tr>
                <th style={{ textAlign: 'left', padding: '8px 0', width: '30%' }}></th>
                <td>{job.smoking_measures}</td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/*  */}
      <div style={{ marginTop: '30px', display: 'flex', gap: '10px' }}>
      <button 
        onClick={() => navigate(`/matching?jobId=${job.id}`)}
        style={{ 
          padding: '10px 20px', 
          backgroundColor: '#4CAF50', 
          color: 'white', 
          border: 'none', 
          borderRadius: '4px', 
          cursor: 'pointer' 
        }}
      >
        
      </button>

        <button
          onClick={() => navigate(`/jobs/edit/${job.id}`)}
          style={{
            padding: '10px 20px',
            backgroundColor: '#2196F3',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer'
          }}
        >
          
        </button>
      </div>
    </div>
  );
}

export default JobDetail;
